  padding: 0;
  margin: 0;
}

body {
  font-family: 'Comic Sans MS', sans-serif;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Jumbotron */

 .hero {
  display: flex;
  align-items: center;
  min-height: 380px;
  width: 100%;
  text-align: center;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-position: center;
  background-size: cover;
}

.skip-link {
  position: absolute;
  top: -100px;
  left: 0;
  background-color: #bf1722;
  color: white;
  padding: 8px;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

.text-primary {
  color: black;
  font-size: 16px;
}

#hamburgerButton {
  padding: 12px 16px;
  background-color: #176B87;
}

/* AppBar */

.app-bar {
  padding: 8px 16px;
  background-color: #176B87;
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.app-bar .app-bar-menu {
  display: flex;
  align-items: center;
}

.app-bar .app-bar-menu button {
  border: none;
  font-size: 18px;
  padding: 8px;
  cursor: pointer;
}

.app-bar .app-bar-brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar-brand h1 {
  color: white;
  text-transform: uppercase;
  font-size: 22px;
  user-select: none;
  font-weight: bolder;
}

.app-bar .app-bar-navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px 0 8px 0;
  background-color: #176B87;
  overflow: hidden;
}

.app-bar .app-bar-navigation.open {
  left: 0;
}

.app-bar .app-bar-navigation ul li a {
  display: inline-block;
  text-decoration: none;
  color: white;
  font-weight: 500;
  padding: 12px 20px 10px;
  margin-bottom: 5px;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.2s ease-in-out;
}

.app-bar-navigation ul li a:hover {
  color: black;
}

/* Main Content */

main {
  padding: 32px;
  flex: 1;
  background-color: #DAFFFB;
}

.content {
  margin: 0 auto;
  min-height: 100%;
}

.content .content-heading {
  text-align: center;
  font-weight: bolder;
}

/* Restaurants */

.restaurants {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 30px;
  margin: 32px 0;
}

/* Restaurant Item */

.restaurant-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  overflow: hidden;
}

.restaurant-item-header {
  position: relative;
}

.restaurant-item .restaurant-item-header .restaurant-item-header-poster {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.restaurant-item .restaurant-item-header .restaurant-item-header-rating {
  position: absolute;
  padding: 8px 12px 8px;
  bottom: 20px;
  left: 0;
  display: inline-block;
  background-color: black;
  color: white;
}

.restaurant-item .restaurant-item-header-rating-score {
  margin-left: 10px;
}

.restaurant-item .restaurant-item-content {
  padding: 20px 20px 25px;
  box-sizing: border-box;
  background-color: white;
}

.restaurant-item .restaurant-item-content h3 {
  text-align: center;
  padding-bottom: 12px;
}

.restaurant-item .restaurant-item-content h3 a {
  color: #176B87;
  text-decoration: none;
  padding: 12px 0 12px;
}

.restaurant-item .restaurant-item-content p {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

/* Restaurant */

.restaurant {
  margin: 0 auto;
  width: 100%;
  max-width: 800px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 18px 16px;
}

.restaurant .restaurant-poster {
  width: 100%;
  max-width: 400px;
}

.information {
  margin-top: 25px;
}

.restaurant .restaurant-info h4 {
  margin: 12px 0 8px;
}

.restaurant-food h4,
.restaurant-drink h4 {
  margin-bottom: 10px;
}

.restaurant-drink {
  margin-top: 24px;
}

.restaurant-description h3 {
  margin: 10px 0 15px;
}

/* LIke */

.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  position: fixed;
  bottom: 50px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  font-size: 18px;
  color: white;
  cursor: pointer;
}

/* Footer */

footer {
  padding: 15px;
}

footer p {
  text-align: center;
  color: #aaaaaa;
  font-size: 12px;
  line-height: 20px;
}

footer p a {
  color: #db0000;
  text-decoration: none;
  padding: 16px 0 16px;
}
